import { Component, OnInit } from '@angular/core';
import { DatagatewayService } from '../datagateway.service';
import { Router } from '@angular/router';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css'],
  providers:[DatagatewayService]
})

export class DashboardComponent implements OnInit {

  constructor(private _DataService:DatagatewayService, private router:Router, private spinnerService: Ng4LoadingSpinnerService) { }

  posts = [];
  user;
  myClickFunction(event) { 
      //just added console.log which will display the event details in browser on click of the button.
      alert("Keyword that you are searching with: "+event);
      if(event){
        this.spinnerService.show();
        this._DataService.getSingleUser(event).subscribe(res =>{
          this.spinnerService.hide();
          this.posts = res; 
          console.log("cu:",this.posts);
        });
      }
      
   }
  ngOnInit() {

    // this.spinnerService.show();
    
  }
}



