import { Component, OnInit } from '@angular/core';
import { FormBuilder,FormGroup,Validators } from '@angular/forms';
import { DatagatewayService } from '../datagateway.service';
import { Router} from '@angular/router';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';

@Component({
  selector: 'app-profileedit',
  templateUrl: './profileedit.component.html',
  styleUrls: ['./profileedit.component.css'],
  providers:[DatagatewayService]
})
export class ProfileeditComponent implements OnInit {

    public regForm:FormGroup;
    constructor(private _DataService:DatagatewayService, private fb:FormBuilder, private router:Router,  private spinnerService: Ng4LoadingSpinnerService) {
      this.regForm = this.fb.group({
        username: ['', Validators.required],
        email: ['', Validators.required],
        dob: ['', Validators.required],
        password: ['', Validators.required],
        confirmPassword: ['', Validators.required],
        city: ['', Validators.required],
        address: ['', Validators.required],
        adhar: ['', Validators.required],
        heq: ['', Validators.required],
        mobile: ['', Validators.required]
      });
    }

    msg = "";
    passMatch = false;

    ngOnInit() {
    }

    register(user){
      this.spinnerService.show();
      let formData = {
        'email':this.regForm.value.email,
        'username':this.regForm.value.username,
        'password':this.regForm.value.password,
        'city':this.regForm.value.city,
        'address':this.regForm.value.address,
        'phone':this.regForm.value.mobile,
        'login':this.regForm.value.username,
        'adhar':this.regForm.value.adhar,
        'high_edu_quali':this.regForm.value.heq,
        'dob':this.regForm.value.dob
      };
      // console.log(JSON.stringify(formData));
      this._DataService.userRegistration(formData).subscribe( res=>{
        this.spinnerService.hide()
        console.log(res);
        if(res.success == true ){
          this.msg = "Successfully Registered"
          // this.router.navigate(['quiz']);
        } else {
          // console.log("User Authentication Failed")
          this.msg = res.error.message;
        }
      });
    }
}



