import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AdminGuard } from './admin.guard';



import { AdminComponent } from './admin/admin.component';
import { AdminUserviewComponent } from './admin-userview/admin-userview.component';
import { UserResultComponent } from './user-result/user-result.component';
import { ResultDisplayComponent } from './result-display/result-display.component';
import { AllusersComponent } from './allusers/allusers.component';

export const ADMIN_ROUTES: Routes = [
    { path: 'admin', component: AdminComponent ,canActivate : [AdminGuard] },
    { path: 'admin/registered-users', component: AdminUserviewComponent ,canActivate : [AdminGuard] },
    { path: 'admin/result/:uId', component: UserResultComponent ,canActivate : [AdminGuard] },
    { path: 'admin/result/:uId/:modId/:levId', component: ResultDisplayComponent ,canActivate : [AdminGuard] },
    { path: 'admin/allusers', component: AllusersComponent ,canActivate : [AdminGuard] },
];


