import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpModule } from '@angular/http';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CountdownModule } from 'ngx-countdown';
import { AuthguardGuard } from './authguard.guard';
import { AdminGuard } from './admin.guard';
import { Ng4LoadingSpinnerModule } from 'ng4-loading-spinner';
import { HashLocationStrategy, Location, LocationStrategy } from '@angular/common';
import { SnotifyModule, SnotifyService, ToastDefaults } from 'ng-snotify';
import { NgxPaginationModule } from 'ngx-pagination';
import { ChartsModule } from 'ng4-charts/ng4-charts';


import { routing, appRoutingProviders } from './app.routing';
import { AppComponent } from './app.component';
import { HeaderComponent } from './header/header.component';
import { FooterComponent } from './footer/footer.component';
import { QuizComponent } from './quiz/quiz.component';
import { PublicComponent } from './public/public.component';
import { SecureComponent } from './secure/secure.component';
import { LoginComponent } from './login/login.component';
import { RegisterComponent } from './register/register.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { ModuleComponent } from './module/module.component';
import { PpqComponent } from './ppq/ppq.component';
import { TaskMaturityComponent } from './task-maturity/task-maturity.component';
import { AdminComponent } from './admin/admin.component';
import { AbstractReasoningComponent } from './abstract-reasoning/abstract-reasoning.component';
import { EngCommComponent } from './eng-comm/eng-comm.component';
import { NaFinanceComponent } from './na-finance/na-finance.component';
import { FinalComponent } from './final/final.component';
import { AdminUserviewComponent } from './admin-userview/admin-userview.component';
import { UserResultComponent } from './user-result/user-result.component';
import { ResultDisplayComponent } from './result-display/result-display.component';
import { ProfileComponent } from './profile/profile.component';
import { ProfileeditComponent } from './profileedit/profileedit.component';
import { AllusersComponent } from './allusers/allusers.component';
import { NaGujratiComponent } from './na-gujrati/na-gujrati.component';
import { ExcelSearchComponent } from './excel-search/excel-search.component';


@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    FooterComponent,
    QuizComponent,
    PublicComponent,
    SecureComponent,
    LoginComponent,
    RegisterComponent,
    DashboardComponent,
    ModuleComponent,
    PpqComponent,
    TaskMaturityComponent,
    AdminComponent,
    AbstractReasoningComponent,
    EngCommComponent,
    NaFinanceComponent,
    FinalComponent,
    AdminUserviewComponent,
    UserResultComponent,
    ResultDisplayComponent,
    ProfileComponent,
    ProfileeditComponent,
    AllusersComponent,
    NaGujratiComponent,
    ExcelSearchComponent
  ],
  imports: [
    BrowserModule,
    HttpModule,
    FormsModule,
    ReactiveFormsModule,
    routing,
    CountdownModule,
    Ng4LoadingSpinnerModule,
    SnotifyModule,
    NgxPaginationModule,
    ChartsModule
  ],
  providers: [
    appRoutingProviders, 
    AuthguardGuard,
    AdminGuard,
    Location,
    { provide: LocationStrategy, useClass: HashLocationStrategy },
    { provide: 'SnotifyToastConfig', useValue: ToastDefaults},
    SnotifyService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



