import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthguardGuard } from './authguard.guard';


import { ModuleComponent } from './module/module.component';
import { QuizComponent } from './quiz/quiz.component';

import { PpqComponent } from './ppq/ppq.component';
import { TaskMaturityComponent } from './task-maturity/task-maturity.component';
import { AbstractReasoningComponent } from './abstract-reasoning/abstract-reasoning.component';
import { EngCommComponent } from './eng-comm/eng-comm.component';
import { NaFinanceComponent } from './na-finance/na-finance.component';
import { FinalComponent } from './final/final.component';
import { ProfileComponent } from './profile/profile.component';
import { ProfileeditComponent } from './profileedit/profileedit.component';
import { ExcelSearchComponent } from './excel-search/excel-search.component';
// import { DashboardComponent } from './dashboard/dashboard.component';

export const SECURE_ROUTES: Routes = [
    // { path: 'dashboard', component: DashboardComponent ,canActivate : [AuthguardGuard] },
    { path: 'module/:id', component: ModuleComponent ,canActivate : [AuthguardGuard] },
    { path: 'module/:id/quiz', component: QuizComponent ,canActivate : [AuthguardGuard] },
    { path: 'module/:id/task-maturity', component: TaskMaturityComponent ,canActivate : [AuthguardGuard] },
    { path: 'module/:id/ppq', component: PpqComponent ,canActivate : [AuthguardGuard] },
    { path: 'module/:id/a-r', component: AbstractReasoningComponent ,canActivate : [AuthguardGuard] },
    { path: 'module/:id/e-c', component: EngCommComponent ,canActivate : [AuthguardGuard] },
    { path: 'module/:id/na-finance', component: NaFinanceComponent ,canActivate : [AuthguardGuard] },
    { path: 'profile', component: ProfileComponent ,canActivate : [AuthguardGuard] },
    { path: 'profile/profile-edit/:id', component: ProfileeditComponent ,canActivate : [AuthguardGuard] },
    { path: 'excel/list/:id', component: ExcelSearchComponent ,canActivate : [AuthguardGuard] }

];


