import { Component, OnInit, ViewChild } from '@angular/core';
import { DatagatewayService } from '../datagateway.service';
import { Router } from '@angular/router';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { CountdownComponent } from 'ngx-countdown';


@Component({
  selector: 'app-eng-comm',
  templateUrl: './eng-comm.component.html',
  styleUrls: ['./eng-comm.component.css'],
  providers:[DatagatewayService]
})
export class EngCommComponent implements OnInit {
  @ViewChild(CountdownComponent) counter: CountdownComponent;
  resetTimer(){
      this.counter.restart();
      this.counter.stop();
      this.counter.pause();
      this.counter.resume();
  }

  constructor(private _DataService:DatagatewayService, private router:Router, private spinnerService: Ng4LoadingSpinnerService) { }
  posts:any = [];
  passages = [];
  currentPassage = [];
  p1_ques = [];
  p2_ques = [];
  fifth_ques = [];
  currentQuestion = {};
  currQuesNo = 0;
  currentuser: any;
  userAns :any = [];
  selected :any = 0;
  crtAnswers = 0;
  atmtAnswers = 0;
  wrngAnswers = 0;
  totalQues = 0;
  isSubmit:boolean = false;
  timer;
  mailMsg:any;

  ngOnInit() {
    this.spinnerService.show();
    this.checkTnC();
    this.currentuser = JSON.parse(sessionStorage.getItem("currentUser"));

    this._DataService.getAllPassages().subscribe(res =>{
      this.passages = res;
      this.currentPassage = this.passages[0].passage.split('+');
      console.log(this.passages[0].passage.split('+'));
      for(let i=0; i<this.currentPassage.length; i++){
        if(this.currentPassage[i].indexOf("+")!=-1){
          this.currentPassage[i].replace("+","");
        } 
      }
    });

    this._DataService.getAllECQuestions().subscribe(res =>{
      this.spinnerService.hide();
      this.posts = res;
      this.totalQues = this.posts.length;
      this.currentQuestion = this.posts[this.currQuesNo];
      this.isActive(this.currQuesNo);
      this.fifth_ques = this.posts[4].que_desc.split("  ");
      console.log("Fifth ques: ", this.fifth_ques);
      for(var i=0; i<this.posts.length; i++){
        if(this.posts[i].passage_id == '1'){
          this.p1_ques.push(this.posts[i]);
        } else {
          this.p2_ques.push(this.posts[i]);
        };
      };


      for (let i=0; i<this.posts.length; i++){
        let userObj = {
          questionId: '',
          ans: '',
          isCorrect:''
        }
        userObj.questionId = this.posts[i].que_id;
        this.userAns.push(userObj); 
      }
      this.resetTimer();
      this.timer = 1800;
    });
  }

  // displayPassage(c){
  //   if(){
  //     return true;
  //   } else return false;
  // };

  checkTnC(){
    if(sessionStorage.getItem("isTnCAgreed") != 'true'){
      this.router.navigate(['module']);
    }
  }
  // Next question
  getNextQues(){
    this.currQuesNo++;
  }

  // Previous question
  getPrevQues(){
    this.currQuesNo--;
  }

  // For getting current question
  getCurrentQuestion(qNo){
    this.currentQuestion = this.posts[qNo];
    this.currQuesNo = qNo;
    if(qNo<8){
      this.currentPassage = this.passages[0].passage.split('+');
    } else this.currentPassage = this.passages[1].passage.split('+');

    for(let i=0; i<this.currentPassage.length; i++){
      if(this.currentPassage[i].indexOf("+")!=-1){
        this.currentPassage[i].replace("+","");
      } 
    }
    // this.select(qNo);
  }

  //For adding active class
  select(item) {
    this.selected = item; 
  };

  //For adding active class
  isActive(item) {
    return this.currQuesNo === item ? 'active': '';
  };

  isAttempted(item) {
    return this.userAns[item].ans == '' ? 'skipped': '';
  };

  

  // For getting the result
  submit(){
    this.checkAnswers();
    let data = {
      "email": this.currentuser.email,
      "user_id": this.currentuser.username,
      "totalQuestions": this.totalQues,
      "totalAttempted": this.atmtAnswers,
      "totalCorrect": this.crtAnswers,
      "totalWrong": this.wrngAnswers
    }
    
    data["testtime"] = this.getCurrentDateTime();
    this._DataService.postUserResultEC(data).subscribe(res=>{
      if(res.success==true){
        this.router.navigate(['module/5']);
      }
    })
  }

  // Function for getting current datetime 
  getCurrentDateTime(){
    let d = new Date();
    let day = d.getDate()<10 ? '0'+d.getDate():d.getDate();
    let month = d.getMonth();
    let months = ['01','02','03','04','05','06','07','08','09','10','11','12'];
    let year = d.getFullYear();
    let hr = d.getHours();
    let min = d.getMinutes();
    let sec = d.getSeconds();
    return year+"-"+months[month]+"-"+day+" "+hr+":"+min+":"+sec;
  };
  
  // For checking the answers
  checkAnswers(){
    for(let i=0; i<this.posts.length; i++){
      if((this.posts[i].true_ans == this.userAns[i].ans)){
        this.crtAnswers++;
      } else if((this.userAns[i].ans != '') && (this.posts[i].true_ans != this.userAns[i].ans)) {
        this.wrngAnswers++;
      }
      if((this.userAns[i].ans != '') && (this.userAns[i].ans != undefined)){
        this.atmtAnswers++;
      }
    }
  }

  checkIfAllAttempted(){
    let totalAttempted = 0;
      for(let i=0; i<this.userAns.length; i++){
        if(this.userAns[i].ans != ''){
          totalAttempted++;
        }
      };
      if(totalAttempted==this.posts.length) {
        return true;
      };
    }

  onFinish(){
    this.submit();
  }

}



