import { Component, OnInit } from '@angular/core';
import { FormBuilder,FormGroup,Validators } from '@angular/forms';
import { DatagatewayService } from '../datagateway.service';
import { Router } from '@angular/router';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css'],
  providers:[DatagatewayService]
})
export class LoginComponent implements OnInit {

  public loginForm:FormGroup;
  constructor(private _DataService:DatagatewayService, private fb:FormBuilder, private router:Router,  private spinnerService: Ng4LoadingSpinnerService) {
    this.loginForm = this.fb.group({
      email: ['', Validators.required],
      password: ['', Validators.required],
    });
  }
  currentUser: any;
  posts: any = [];
  errMsg = "";

  login(user){
    this.spinnerService.show();
          this.router.navigate(['dashboard']);
  }

  

  ngOnInit() {
  }

}



