import { Component, OnInit } from '@angular/core';
import { DatagatewayService } from '../datagateway.service';
import { Router } from '@angular/router';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';

@Component({
  selector: 'app-allusers',
  templateUrl: './allusers.component.html',
  styleUrls: ['./allusers.component.css'],
  providers:[DatagatewayService]
})
export class AllusersComponent implements OnInit {

  constructor(private _DataService:DatagatewayService, private router:Router, private spinnerService: Ng4LoadingSpinnerService) { }

  posts = [];
  user;
  usercount;
  email;
  status;
  ngOnInit() {
    
    this.user=JSON.parse(sessionStorage.getItem("currentUser"));
    // console.log(this.user.username);
    this.spinnerService.show();
    this._DataService.getAllUsers().subscribe(res =>{
        this.spinnerService.hide();
        this.posts = res; 
        // console.log("cu",this.posts);
      });
      
      this._DataService.getAllUsersCount().subscribe(res =>{
        this.usercount = res[0]; 
        console.log("cu",this.usercount);
      }); 
      
  }
   changeStatus(email,status){
     if(status == "Active")
     {
        status="Deactive";
     }
     else
     {
      status="Active";
     }
     let sts = {
       "status": status
     }
    this._DataService.updateSingleUserStatus(email,sts).subscribe(res =>{
      // this.status = res[0].email; 
       console.log("status change result",res);

       this._DataService.getAllUsers().subscribe(res =>{
        this.posts = res; 
      });

    });  
   }

}



