import { Component, OnInit } from '@angular/core';
import { DatagatewayService } from '../datagateway.service';
import { ActivatedRoute,Router } from '@angular/router';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';

@Component({
  selector: 'app-result-display',
  templateUrl: './result-display.component.html',
  styleUrls: ['./result-display.component.css'],
  providers:[DatagatewayService]
})
export class ResultDisplayComponent implements OnInit {

  constructor(private _DataService:DatagatewayService, private router:Router, private activeRoute:ActivatedRoute, private spinnerService: Ng4LoadingSpinnerService) { }

  module;
  level;
  userId;
  user;
  ppqScore;
  NA;
  NA2;
  AR;
  EC;
  posts = [];

  // Chart.js configurations 
  barChartOptions:any = {
    scaleShowVerticalLines: false,
    responsive: true
  };
  barChartLabels:string[] = ['Assertive', 'Reliable', 'Frustration', 'Social', 'Careful', 'Original Ideas', 'People', 'Vitality'];
  barChartType:string = 'bar';
  barChartLegend:boolean = false;
  barChartData:any[] = [
    {data: [], label: 'PPQ'}
  ];
  // Chart.js configurations ends here

  ngOnInit() {
    this.spinnerService.show;
    this.activeRoute.params.subscribe( params =>{
      this.module = params['modId'];
      this.level = params['levId'];
      this.userId = params['uId'];
    });
    this.user=JSON.parse(sessionStorage.getItem("currentUser"));
    console.log(this.user.username);
    this.spinnerService.show();
    this._DataService.getSingleUser(this.user.username).subscribe(res =>{
        this.spinnerService.hide();
        this.posts = res; 
         console.log("cu",this.posts);
      });
    this._DataService.getUserPPQScore(this.userId).subscribe(res =>{
      console.log(res);
      this.ppqScore = res[0];
      this.barChartData = [
        {backgroundColor: "rgba(2,117,216,1)",
         borderColor: "rgba(2,117,216,1)",
         data: [this.ppqScore.Assertive_A,
          this.ppqScore.Reliable_R,
          this.ppqScore.Frustration_F,
          this.ppqScore.Social_S,
          this.ppqScore.Careful_C,
          this.ppqScore.Original_Ideas_O,
          this.ppqScore.People_P,
          this.ppqScore.Vitality_V],}
      ];
    });
    this._DataService.getSingleUser(this.userId).subscribe(res =>{
      this.user = res[0].username;
      this.spinnerService.hide;
      console.log("cu",this.posts);
    });


    //Result Displayed for NA
    this._DataService.getSingleUserResult(this.userId).subscribe(res =>{
      
      console.log(res);

      this.NA = res[0];
      this.NA.Total_Attempted_Questions;
      this.NA.Total_Correct_Answers;
      this.NA.Total_Wrong_Answers;
      this.NA.testtime;
      
      
    });
    this._DataService.getSingleUser(this.userId).subscribe(res =>{
      this.user = res[0].username;
      this.spinnerService.hide;
      console.log("cu",this.posts);
    });

    //Result Displayed for NA Part 2
    this._DataService.getSingleUserResultNA(this.userId).subscribe(res =>{
      
      console.log(res);

      this.NA2 = res[0];
      this.NA2.total_ques_attemted;
      this.NA2.total_right_answer;
      this.NA2.total_wrong_answer;
      this.NA2.testtime;
      
      
    });
    this._DataService.getSingleUser(this.userId).subscribe(res =>{
      this.user = res[0].username;
      this.spinnerService.hide;
      console.log("cu",this.posts);
    });

    //Result Displayed for AR
    this._DataService.getSingleUserResultAR(this.userId).subscribe(res =>{
      
      console.log(res);

      this.AR = res[0];
      this.AR.total_ques_attemted;
      this.AR.total_right_answer;
      this.AR.total_wrong_answer;
      this.AR.testtime;
      
      
    });
    this._DataService.getSingleUser(this.userId).subscribe(res =>{
      this.user = res[0].username;
      this.spinnerService.hide;
      console.log("cu",this.posts);
    });



    //Result Displayed for EC
    this._DataService. getSingleUserResultEC(this.userId).subscribe(res =>{
      
      console.log(res);

      this.EC = res[0];
      this.EC.total_ques_attemted;
      this.EC.total_right_answer;
      this.EC.total_wrong_answer;
      this.EC.testtime;
      
      
      
    });
    this._DataService.getSingleUser(this.userId).subscribe(res =>{
      this.user = res[0].username;
      this.spinnerService.hide;
      console.log("cu",this.posts);
    });
  }

}



